"use client"

import { useState, useEffect } from "react"
import { Sidebar } from "@/components/sidebar"
import { Header } from "@/components/header"
import { DataTable } from "@/components/data-table"
import { RecordDetailModal } from "@/components/record-detail-modal"
import { useCRMStore } from "@/lib/store"
import { sampleProtectRecords, sampleSettlementRecords } from "@/lib/sample-data"
import type { CRMRecord } from "@/lib/types"

export default function ProtectPage() {
  const {
    records,
    setRecords,
    searchQuery,
    setSearchQuery,
    statusFilter,
    setStatusFilter,
    partnerFilter,
    setPartnerFilter,
    dpdGroupFilter,
    setDpdGroupFilter,
    dateRangeStart,
    dateRangeEnd,
    setDateRange,
    getFilteredProtectRecords,
    deleteRecord,
  } = useCRMStore()

  const [selectedRecord, setSelectedRecord] = useState<CRMRecord | null>(null)
  const [isModalOpen, setIsModalOpen] = useState(false)
  const [isHydrated, setIsHydrated] = useState(false)

  useEffect(() => {
    setIsHydrated(true)
    if (records.length === 0) {
      setRecords([...sampleProtectRecords, ...sampleSettlementRecords])
    }
  }, [records.length, setRecords])

  const filteredRecords = getFilteredProtectRecords()

  const handleViewRecord = (record: CRMRecord) => {
    setSelectedRecord(record)
    setIsModalOpen(true)
  }

  if (!isHydrated) {
    return (
      <div className="min-h-screen bg-background flex items-center justify-center">
        <div className="animate-pulse text-muted-foreground">Loading...</div>
      </div>
    )
  }

  return (
    <div className="min-h-screen bg-background">
      <Sidebar />
      <main className="ml-64">
        <Header title="Protect" subtitle="Manage protect requests and EMI protection cases" />

        <div className="p-6">
          <DataTable
            title="Protect Records"
            records={filteredRecords}
            type="protect"
            searchQuery={searchQuery}
            setSearchQuery={setSearchQuery}
            statusFilter={statusFilter}
            setStatusFilter={setStatusFilter}
            partnerFilter={partnerFilter}
            setPartnerFilter={setPartnerFilter}
            dpdGroupFilter={dpdGroupFilter}
            setDpdGroupFilter={setDpdGroupFilter}
            dateRangeStart={dateRangeStart}
            dateRangeEnd={dateRangeEnd}
            setDateRange={setDateRange}
            onViewRecord={handleViewRecord}
            onDeleteRecord={deleteRecord}
          />
        </div>
      </main>

      <RecordDetailModal record={selectedRecord} open={isModalOpen} onClose={() => setIsModalOpen(false)} />
    </div>
  )
}
